.services {
  &__container {
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 90px;
  }

  &__content {
    padding: 0 20px;

    display: flex;
  }

  &__content-left {
    position: relative;

    padding: 100px 0;
    width: 50%;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 75px;

    border: 1px solid #c6c6c6;
    border-radius: 16px;
    background-color: #eaebed;

    ::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(0%, 70%);
      width: 580px;
      height: 50px;
      z-index: -1;
      border-radius: 16px;
      background-color: #598be0;
    }
  }

  &__tabs {
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(-47%);
    z-index: -1;
  }

  &__text {
    margin: 0;

    font-weight: 400;
    font-size: 18px;
    color: #000;
  }

  &__text-up {
    padding: 25px;

    max-width: 390px;

    border-radius: 10px;
    background-color: #fff;
  }

  &__bold {
    font-weight: 600;
    font-size: 20px;
    line-height: 125%;
    letter-spacing: -0.03em;
    color: #131313;
  }

  &__hr {
    margin: 0;

    width: 440px;

    border-color: #fff;
  }

  &__text-down {
    padding: 25px;

    max-width: 390px;
  }

  &__content-right {
    width: 50%;

    border: 1px solid #dadada;
    border-radius: 16px;
  }

  &__list {
    padding: 30px 0;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 50px;

    :not(:last-of-type) {
      border-bottom: 1px solid #dadada;
    }
  }

  &__item {
    padding-bottom: 20px;
    min-width: 450px;
    display: flex;
    align-items: center;
    gap: 40px;

    // ::after {
    //   content: "";
    //   position: absolute;
    //   top: 10px;
    //   left: 0;
    //   width: 100%;
    //   border-bottom: 2px solid #111;
    // }
  }

  &__circle {
    width: 40px;
    height: 40px;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 50%;
    background: #598be0;
  }

  &__image {
  }

  &__text--list {
    max-width: 370px;
  }
}
