.about {
  background-color: #f5f5f5;

  &__container {
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 60px;
    row-gap: 50px;
  }

  &__item {
    grid-column: 3 span;
  }

  &__item--image {
    position: relative;
  }

  &__article {
    max-width: 273px;
  }
}

.article {
  &__number {
    max-width: 44px;

    margin-bottom: 12px;
    padding: 12px 18px;

    display: block;

    font-family: var(--second-family);
    font-weight: 600;
    font-size: 16px;
    color: #598be0;

    border-radius: 10px;
    background-color: #fff;
  }

  &__name {
    margin: 0;
    margin-bottom: 5px;

    font-weight: 400;
    font-size: 20px;
    color: #000;
  }

  &__info {
    margin: 0;

    font-weight: 400;
    font-size: 16px;
    color: #000;
  }

  &__image {

  }

  &__image--top {
    position: absolute;
    top: -85px;
  }
}
