.history {
  &__container {
    display: flex;
    flex-direction: column;
    gap: 70px;
  }

  &__subtitle {
    margin: 0;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  &__grid {
  }

  &__grid-up {
  }

  &__list--up {
    display: grid;
    grid-template-columns: 250px 150px 250px 100px 335px;
    grid-template-areas: "first . second . third .";
  }

  &__item {
    grid-column: 1 span;
  }

  &__item--up {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  &__item--first {
    grid-area: first;
  }

  &__item--third {
    grid-area: second;
  }

  &__item--fifth {
    grid-area: third;
  }

  &__date {
    margin: 0;

    font-weight: 400;
    font-size: 20px;
    color: #000;
  }

  &__descr {
    margin: 0;

    font-weight: 400;
    font-size: 16px;
    color: #000;
  }

  &__grid-bottom {
  }

  &__list--bottom {
    display: grid;
    grid-template-columns: 220px 250px 70px 250px 220px;
    grid-template-areas: ". first . second . third";
  }

  &__item--second {
    grid-area: first;
  }

  &__item--fourth {
    grid-area: second;
  }

  &__item--sixth {
    grid-area: third;
    max-width: 335px;
  }
}
