.shape {
  background-color: #222;

  &__container {
  }

  &__content {
    position: relative;

    display: flex;

    min-height: 535px;

    border-radius: 30px;
  }

  &__content-left {
    padding: 50px 35px;

    min-width: 454px;

    display: flex;
    flex-direction: column;

    border-radius: 30px;
    background-color: #fff;
  }

  &__content-right {
    position: relative;

    padding: 60px 50px 60px 67px;
    width: 100%;

    display: flex;
    flex-direction: column;

    border-radius: 30px;
    background-color: #fff;
  }
}

.shape__content-right::after {
  content: "";
  position: absolute;
  top: 35px;
  left: 0;
  transform: translate(-50%);
  width: 10px;
  height: 460px;
  background-image: url("../../img/form-hr.svg");
}

.form {
  display: flex;
  flex-direction: column;
  flex: 1;


  &__input {
    display: inline-block;

    width: 100%;
    font-weight: 600;
    font-size: 24px;
    color: rgba(0, 0, 0, 0.5);
    border-bottom: 2px solid rgba(0, 0, 0, 0.5);


    &:not(:nth-child(2)) {
      margin-bottom: 70px;
    }
    // ::before {
    //   content: "Имя";
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   width: 50px;
    //   height: 30px;
    // }
  }

  &__btn {
    display: flex;
    margin-top: auto;
    padding: 12px 75px;

    max-width: 240px;

    font-weight: 600;
    font-size: 25px;
    text-align: center;
    color: #fff;
    border-radius: 20px;
    background-color: #ff671b;
  }
}

.price {
  &__title {
    margin: 0;
    margin-bottom: 55px;

    font-weight: 600;
    font-size: 49px;
    color: #000;
    text-align: center;
  }

  &__value {
    margin: 0;
  }

  &__value-new {
    margin-bottom: 22px;
    padding: 10px 25px 10px 50px;

    font-weight: 600;
    font-size: 75px;
    color: #fff;
    border-radius: 50px;
    background-color: #598be0;
  }

  &__value-old {
    padding-left: 50px;

    font-weight: 600;
    font-size: 40px;
    text-decoration: line-through;
    color: rgba(0, 0, 0, 0.7);
  }

  &__discount {
    margin-top: auto;
    padding: 13px 21px;

    font-weight: 600;
    font-size: 22px;
    text-align: center;
    color: #ff671b;
    border-radius: 20px;
    background-color: #fff0e9;
  }
}

.fields {
  &__title {
    margin: 0;

    font-weight: 600;
    font-size: 34px;
    color: #000;
  }

  &__form {
    margin-top: 115px;
  }
}
