.reviews {
  &__container {
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
  }

  &__descr {
    margin: 0 auto;

    display: block;

    max-width: 720px;

    font-weight: 600;
    text-align: center;
    font-size: 20px;
    color: #747474;
  }

  &__wrapper {
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }

  &__content {
    margin: 60px 0 45px;
  }

  &__item {
    padding: 20px;

    border-radius: 10px;
    background-color: #eaebed;
  }

  &__image {
  }

  &__content-inner {
    display: flex;
    justify-content: space-between;
    gap: 28px;
  }

  &__card {
    width: 100%;
  }

  &__card--white {
    max-width: 500px;

    border-radius: 10px;
    background-color: #eaebed;
  }

  &__card--black {
    max-width: 672px;

    border-radius: 10px;
    background-color: #222;
  }
}

.swiper {
}
.swiper-wrapper {
}
.swiper-slide {
  width: 330px;
}

.card {
  &__text {
    margin: 0;
    padding: 24px 10px;

    font-weight: 400;
    font-size: 20px;
    text-align: center;
    color: #598be0;
  }
}
