.advantages {
  background-color: #222;

  &__container {
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 50px;

    color: #fff;
  }

  &__content {
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 13px;
    row-gap: 8px;
  }

  &__item {
    display: flex;
    grid-column: 1 span;

    background: #fff;
    border-radius: 10px;
  }

  &__item--big {
    grid-column: 2 span;
    grid-column-start: 2;
    grid-column-end: 4;

    grid-row: 2 span;
  }

  &__item--full {
    grid-column: 4 span;
  }

  &__image {
    padding: 90px 23px;

    width: 100%;
  }
}

.item {
  &__card {
    margin: 0 auto;
    width: 100%;
    max-width: 235px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    gap: 15px;
  }

  &__card--full {
    margin: 0 auto;
    padding: 44px 0;
    display: flex;
    align-items: center;

    max-width: 911px;
    gap: 70px;
  }

  &__name {
    font-weight: 600;
    font-size: 18px;
    color: #598be0;
  }

  &__descr {
    margin: 0;

    font-weight: 400;
    font-size: 14px;
    color: #000;
  }
}
